import api from "./../../server/api";

const state={
    keyWord:"",
    history:[],
    searchResult:[],
    hot:[],
    setSearchSort:""
}
const getters={

}
const actions={
    // 搜索
    loadDataSearch({commit}){
        api.loadDataBySearch({
            platform:"H5",
            code:"hotWord,sketch"
        }).then(({data})=>{
            commit("setSearchResult",data);
            commit("setHot",data.hotWord.data);
            // console.log(data.hotWord.data);
        })
    },
    loadDataSearch2({commit},{keyWord,sortKey="pop"}){
        api.loadDataBySorts({
            q:keyWord,
            cKey:"46",
            page:"1",
            sort:sortKey,
        }).then(({result})=>{
            commit("setSearchResult",result.wall.docs);
            // console.log("===>",result.wall.docs)
        })
    },
    loadSearchTitle({commit}){
        commit("setSort",[]);
        api.loadDataBySorts({
            page:"1",
            cKey:"h5-cube",
        }).then(({result})=>{
            commit("setSearchSort",result.sortFilter);
            // console.log(result.sortFilter)
        })
    },
}
const mutations={
    setKeyWord(state,data){
        state.keyWord=data;
    },
    setHistory(state,data){
        state.history=data;
    },
    setSearchResult(state,data){
        state.searchResult=data;
    },
    setHot(state,data){
        state.hot=data;
    },
    setSearchSort(state,data){
        state.searchSort=data;
    }
}
export default {
    state,
    getters,
    actions,
    mutations
}




