<template>
    <div class="search">
        <div class="search-top">
            <i class="iconfont icon-fanhui" @click="search_back()"></i>
            <input type="text" placeholder="搜索" v-model="search.keyWord">
            <span @click="searchAll()">搜索</span>
        </div>
        <div class="search-fly">
            <div class="histroy">
                <div class="history-title">
                    <i class="iconfont icon-sousuo"></i>
                    <span>历史记录</span>
                    <i class="iconfont icon-shanchu" @click="clearRecord()"></i>
                </div>
                <div class="record">
                    <span v-for="(v,i) of search.history" :key="i" @click="selectHistory($event)">{{v}}</span>
                </div>
            </div>
            <div class="search-hot">
                <div class="search-hot-title">
                    <i class="iconfont icon-shoucang"></i>
                    <span>热门搜索</span>
                </div>
                <div class="record">
                    <span v-for="(v,i) of search.hot" :key="i+'i'" @click="selectHot(v)">{{v.frontword}}</span>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import Router from "vue-router";
import {mapState,mapGetters,mapActions,mapMutations} from "vuex"
export default {
    data(){
        return {
            arr:[]
        }
    },
    computed:{
        ...mapState(["search","types"])
    },
    methods:{
        ...mapActions(["loadDataSearch2","loadDataSearch","loadDataBySorts"]),
        ...mapMutations(["setKeyWord","setHot"]),
        search_back(){
            this.$router.back(-1);
        },

        // 点击搜索按钮
        searchAll(){
            this.$router.push("/searchList");
        },
        selectHistory(e){
            this.keyWord=e.target.innerText;
            searchAll(e.target.innerText);
            
        },
        clearRecord(){
            // history=[];   // 为什么此处不能写为 this.arr.length=0;
            localStorage.clear();
        },
        selectHot(v){
            console.log(v.frontword);
            this.setKeyWord(v.frontword);
            this.searchAll();
        }
    },
    mounted(){
        this.loadDataSearch()
    }
}
</script>
<style lang="less">
    @searchTop-height:80px;/*px*/
    .search{
        width: 100%;
        height: 100%;/*px*/
        color: #666;
        font-size: 26px;/*px*/
        
        &-top{
            width: 100%;
            height:  80px;/*px*/
            // height:  @searchTop-height;
            padding: 0 20px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            border-bottom: 1px solid #eee;
            i{
                font-size: 38px;/*px*/
            }
            input{
                border: 1px solid #666;
                text-indent: 15px;
                width: 80%;
                height: 45px;/*px*/
                background: transparent;
                line-height: 45px;
                border-radius: 10px;
                font-size: 24px;/*px*/
            }
            input:focus{
                border: 1px solid #FD617F;
                outline: none;
            }
            input::-webkit-input-placeholder{
                color: #999;
                font-size: 24px;/*px*/
            }
        }
        &-fly{
            width: 100%;
            height: 90%;  // 怎么样设为100%-80px???
            // height: (100%-@searchTop-height);  // 怎么样设为100%-80px???
            margin-top: 10px;
            padding-bottom: 80px;
            position: absolute;
            top: 80px;
            border: 1px solid green;
            // ////////////////////
            @keyframes fadeInUpBig {
                from {
                    opacity: 0;
                    transform: translate3d(0, 2000px, 0);
                }
                to {
                    opacity: 1;
                    transform: translate3d(0, 0, 0);
                }
            }
            .fadeInUpBig {
                animation-name: fadeInUpBig;
            }
            // ////////////////////
            .histroy{
                width: 100%;
                padding: 18px 15px;
                border-bottom: 1px solid #eee;
                i{
                    font-size: 30px;/*px*/
                }
                .history-title{
                    .icon-shanchu{
                        float: right;
                    }
                }
            }
            .search-hot{
                padding: 18px 15px;
                    i{
                        font-size: 30px;/*px*/
                    }
                }
                .record{
                    display: flex;
                    flex-wrap: wrap;
                    padding: 18px 20px;
                    span{
                        border: 1px solid #ddd;
                        padding: 0.02rem 0.16rem;
                        border-radius: 0.08rem;
                        margin-bottom: 0.2rem;
                        margin-right: 0.2rem;
                    }
                }
            }
        }
</style>



