<template>
    <div class="tb-left wrapper" ref="wrapper_l">
        <div class="content">
            <ul>
                <li v-for="(v,i) of types.left" :key="'left'+i" @click="selectType(v.maitKey,v.miniWallkey)">
                    <span>{{v.title}}</span>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
import Bscroll from 'better-scroll';
import {mapState,mapActions,mapGetters,mapMutations} from "vuex";
export default {
    computed:{
        ...mapState(["types"])
    },
    methods:{
        ...mapActions(["loadTypesData",
                       "loadTypesDataTop",
                       "loadTypesDataTitle",
                       "loadTypesDataBottom"
                      ]),
        ...mapMutations(["setCcid"]),
        selectType(maitKey,miniWallKey){
            this.loadTypesDataTop(maitKey);
            this.loadTypesDataTitle(miniWallKey);
            this.loadTypesDataBottom({miniWallKey,maitKey:"pop"});
            this.setCcid(miniWallKey);
        }
    },
    mounted() {
        this.$nextTick(() => {
            this.scroll = new Bscroll(this.$refs.wrapper_l, {
                click:true
            })
        });
        this.loadTypesData();
        this.loadTypesDataBottom({miniWallKey:"10062603",maitKey:"pop"});
    }
}
</script>
<style lang="less">
    .tb-left{
        width: 25%;
        height: 100%;/*px*/
        background: #F6F6F6;
        ul li{
            font-size: 28px; /*px*/
            text-align: center;
            padding: 24px 0;
            span{
                display: inline-block;
                border-left:4px solid transparent;
                width: 100%;
                // 左侧点击颜色#FC5779
            }
        }
    }
</style>




