<template>
    <div class="home">
        <top-bar class="nav"></top-bar>
        <div class="hb-parent">
            <home-body></home-body>
        </div>
        <bottom-bar class="footer"></bottom-bar>
    </div>
</template>
<script>
import HomeBody from "./HomeBody";
export default {
    components:{
        HomeBody
    }
}
</script>
<style lang="less">
    .home{
        height: 100%;
        background: #eee;
        .hb-parent{
            height: 100%;
            padding: 100px 0px;
        }
        .nav{
            position: fixed;
            top: 0;
            z-index: 99;
        }
        .footer{
            position: fixed;
            bottom: 0;
        }
    }
</style>



