<template>
    <div class="tb">
        <i class="iconfont icon-xx"></i>
        <router-link to="/search" class="tb_search" tag="div" @click="tb_search()">
            <i class="iconfont icon-sousuo tb-search"></i>
            <input type="text">
        </router-link>
        <i class="iconfont icon-gouwuche"></i>
    </div>
</template>
<script>
export default {
    methods:{
        // 不能写横线，所以用下划线
        tb_search(){
            // console.log("tb-search被点击");
            this.$router.push("/search");
        }
    }
}
</script>
<style lang="less">
    .tb{
        width: 100%;
        height: 100px;
        border-bottom: 1px solid #eee;
        display: flex;
        justify-content: space-around;
        align-items: center;
        background: #fff;
        // 搜索栏的div
        &_search{
            width: 80%;
            height: 60px;
            background: #eee;
            border-radius: 8px;
            position: relative;
            input{
                width: 100%;
                height: 60px;
                border: none;
                border-radius: 8px;
                outline: none;
                background: transparent;
                text-indent: 56px;
                font-size: 30px;
            }
        }
        .iconfont{
            font-size: 35px;
        }
        // 放大镜的绝对定位
        &-search{
            position: absolute;
            left: 10px;/*px*/
            top: 10px;/*px*/
            color: #666;
        }
    }
</style>


