<template>
    <div class="tl">
        <top-bar class="top-fixed"></top-bar>
        <div class="sort">
            <span v-for="(v,i) of types.sort" :key="'sort'+i" @click="selectSort(v.sortKey)">{{v.title}}</span>
        </div>
        <div class="tl wrapper" ref="wrapper">
            <div class="content">
                <div class="goods-parent">
                    <goods :typesgoods="types.sortbottom[i]" v-for="(v,i) of types.sortbottom" :key="'sortbottom'+i" class="goods">
                        <b slot="pink">￥{{v.price}}元</b>   <!-- 需要过滤器 -->
                    </goods>
                    <div class="btw" v-if="true"></div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import Bscroll from "better-scroll";
import {mapState,mapGetters,mapActions,mapMutations} from "vuex"
export default {
    computed:{
        ...mapState(["types","search"])
    },
    methods:{
        ...mapActions(["loadTypesDataBottom"]),
        ...mapMutations(["setSortBottom"]),
        selectSort(sortKey){
            this.loadTypesDataBottom({miniWallKey:this.types.typeId,sortKey});
        }
    },
    mounted(){
        this.$nextTick(() => {
            this.scroll = new Bscroll(this.$refs.wrapper, {
                click:true
            })
        });
        var a=this.$route.fullPath;
        var aa=a.slice(a.indexOf("=")+1);
        this.loadTypesDataBottom({miniWallKey:aa,sortKey:"pop"});

    }
}
</script>
<style lang="less">
    .tl{
        width: 100%;
        height: 100%;
        .top-fixed{
            position: fixed;
            top: 0;
            z-index: 99;
        }
        .sort{
            width: 90%;
            padding: 20px 0;
            margin: 100px 0 10px 5%;
            border-top: 1px solid #aaa;
            border-bottom: 1px solid #aaa;
            span{
                display: inline-block;
                width: 32%;
                text-align: center;
            }
            span:nth-child(2){
                border-right: 1px solid #aaa;
                border-left: 1px solid #aaa;
            }
        }
        .goods-parent{
            width: 100%;
            height:100%;
            display: flex;
            flex-wrap: wrap;
            flex-grow: 2;
            justify-content: space-around;
            .goods{
                b{
                    color: #FF5777;
                    font-size: 26px;
                }
            }
            .btw{
                width: 48%;
            }
        }
    }
</style>



