<template>
    <div class="gd wrapper" ref="wrapper">
        <div class="content">
            <div class="gd-top gd-background padding">
                <!-- 商品展示图 -->
                <div class="gd-img">
                    <img :src="types.details.img">
                </div>
                <!-- 商品描述 -->
                <p class="gd-desc">{{types.details.title}}</p>
                <!-- 价格 -->
                <div class="gd-price">
                    <div>￥</div>
                    <div class="gd-rmb">{{types.details.price | number}}</div>
                    <div class="gd-sale">
                        <del>￥{{types.details.orgPrice | number}}</del>
                        <p>{{sale}}折</p>
                    </div>
                </div>
                <!-- 免邮费 -->
                <div class="gd-post">免邮费</div>
                <div class="hr"></div>
                <div class="gd-7days"></div>
            </div>
            <!-- 72小时发货 -->
            <div class="gd-background padding">
                <div class="gd-7days">
                    <p>
                        <i class="iconfont icon-52"></i>
                        <span>72小时发货</span>
                    </p>
                    <p>
                        <i class="iconfont icon-52"></i>
                        <span>7天无理由退货</span>
                    </p>
                    <p>
                        <i class="iconfont icon-52"></i>
                        <span>退货补运费</span>
                    </p>
                    <p>
                        <i class="iconfont icon-angleright"></i>
                    </p>
                </div>
            </div>
            <!-- 选择颜色 -->
            <div class="gd-chioseColor gd-background padding">
                <p>请选择：颜色 尺寸</p>
                <i class="iconfont icon-angleright"></i>
            </div>
            <!-- 评论 -->
            <div class="gd-comment gd-background padding">
                <div class="gd-comment-title">
                    <p>买家评价 {{comment.comment.total}} | 销量 662</p>
                    <i class="iconfont icon-angleright"></i>
                </div>
                <div class="gd-comment-keywords">
                    <span v-for="(v,i) of comment.comment.rateTags" :key="i">{{v.value+'('+v.num+')'}}</span>
                </div>
                <div class="gd-comment-body" v-for="(v,i) of comment.comment.list" :key="i">
                    <div class="gd-comment-nikename">
                        <img :src="v.user.avatar">
                        <span>{{v.user.uname}}</span>
                    </div>
                    <div class="gd-comment-content">
                        <p>{{v.content}}</p>
                        <span>{{v.time}}</span>
                        <p>
                            <span>{{v.style}}</span>
                        </p>
                    </div>
                    <div class="hr"></div>
                </div>
            </div>
        </div>
        <!-- 底部粘连 -->
        <div class="gd-bottom">
            <div class="gd-bottom-collect collShop">
                <i class="iconfont icon-shoucang1"></i>
                <span>收藏</span>
            </div>
            <div class="gd-bottom-shopping collShop">
                <i class="iconfont icon-dianpu"></i>
                <span>小店</span>
            </div>
            <div tag="div" class="gd-bottom-shoppingCar" @click="toCar()">加入购物车</div>
            <div class="gd-bottom-rightBuy">立即购买</div>
        </div>
        
    </div>
</template>
<script>
import Bscroll from "better-scroll";
import {mapState,mapGetters,mapActions,mapMutations} from "vuex"
export default {
    data(){
        return{
            date:null,
        }
    },
    computed:{
        ...mapState(["types","comment","carList"]),
        sale(){
            var sale=this.types.details.price/this.types.details.orgPrice;
            return sale.toFixed(1)*10;
        }
    },
    methods:{
        ...mapActions(["loadDataComment"]),
        ...mapMutations(["setCarList"]),
        toCar(){
            this.$router.push("/car");
            this.setCarList(this.types.details);
            // console.log(this.types.details);
        },
        selectComment(){
            console.log(100)
        },
    },
    filters:{
        number(value){
            // console.log(s);
            return value.toFixed(2);

        }
    },
    mounted() {
        this.$nextTick(() => {
            this.scroll = new Bscroll(this.$refs.wrapper, {
                click:true
            })
        });
        this.loadDataComment(this.types.details.iid);
        
    }
}
</script>
<style lang="less">
    .gd{
        width: 100%;
        height: 100%;
        font-size: 26px;/*px*/
        padding-bottom: 80px;
        // 整个页面的背景色（灰色）
        background: #eaeaea;
        // 白色背景
        &-background{
            background: #fff;
        }
        // 向右的箭头颜色
        .icon-angleright{
            color: #555;
        }
        // 全局样式，左右的padding
        .padding{
            padding: 0 16px;
        }
        &-top{
            // 大图片
            .gd-img{
                img{
                    width: 80%;
                    border-radius: 6px;
                    overflow: hidden;
                    overflow-wrap: nowrap;
                }
            }
            // 描述
            .gd-desc{
                font-size: 30px;/*px*/
                color: #444;
                margin: 10px 0;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 2;
                overflow: hidden;
            }
            // 价格
            .gd-price{
                display: flex;
                .gd-rmb{
                    font-size: 60px;/*px*/
                    font-weight: bold;
                    // border:1px solid green;
                }
                .gd-sale{
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    del{
                        color: #666;
                    }
                    >p{
                        background: #FEE8EE;
                        text-align: center;
                        border-radius: 6px;
                        color: #FC285C;
                    }
                }
            }
            // 邮费
            .gd-post{
                color: #666;
                margin: 16px 0;
                
            }
            // 水平线
            .hr{
                border: 0.5px solid #ddd;
            }
        }
        // 7天无理由退货
        &-7days{
            font-size: 28px;/*px*/
            padding-bottom: 20px;
            display: flex;
            justify-content: space-between;
            span{
                color: #555;
            }
            .icon-52{
                color:#FB5F62;
                font-weight: bold;
                font-size: 30px;/*px*/
            }
        }
        .icon-angleright{
            font-size: 30px;/*px*/
        }
        // 选择颜色尺寸
        &-chioseColor{
            p{
                font-size: 26px;/*px*/
                font-weight: bold;
                color: #777;
            }
            margin: 10px 0;
            height: 60px;/*px*/
            line-height: 60px;
            display: flex;
            justify-content: space-between;
        }
        // 评论
        &-comment{
            // 腾出底部的位置
            margin-bottom: 60px;
            .gd-comment-title{
                p{
                    color: #999;
                }
                display: flex;
                justify-content: space-between;
                height: 60px;/*px*/
                line-height: 60px;
                font-size: 23px;/*px*/
            }
            .gd-comment-keywords{
                span{
                    color:#FC2858;
                    background: #FEE8EE;
                    padding: 6px 12px;
                    border-radius: 0.05rem;
                    margin-top:10px;
                    margin-right: 10px;
                    white-space: nowrap;
                    display: inline-block;
                }
                padding: 20px 0;
            }
            .gd-comment-body{
                color: #555;
                .gd-comment-nikename{
                    margin: 8px 0;
                    img{
                        width: 80px;
                        // height: 80px;/*px*/
                        border-radius: 50%;
                    }
                    span{
                        // vertical-align: bottom;
                    }
                }
            }
            .gd-comment-content{
                p{
                    font-size: 26px;/*px*/
                }
                span{
                    color: #888;
                }
            }
            .hr{
                border:0.5px solid #eee;
                margin: 25px 0;
            }
        }
        // 底部粘连
        &-bottom{
            width: 100%;
            height: 80px;/*px*/
            position: fixed;
            bottom: 0;
            background: #fff;
            display: flex;
            border-top: 1px solid #eee;
            .collShop{
                width: 15%;
                height: 100%;/*px*/
                padding: 5px;
                font-size: 22px;/*px*/
                color: #888;
                text-align: center;
                display: flex;
                flex-direction: column;
                justify-content: space-around;
                i{
                    font-size: 30px;
                }
            }
            .gd-bottom-collect{
                border-right: 0.6px solid #ccc;
            }
            .gd-bottom-shoppingCar{
                width: 35%;
                color:#FC5979;
                text-align: center;
                background: #FEE6E8;
                font-size: 28px;/*px*/
                line-height: 80px;
                font-weight: bold;
            }
            .gd-bottom-rightBuy{
                width: 35%;
                color: #fff;
                font-size: 28px;
                text-align: center;
                background: #FC4C8B;
                line-height: 80px;
            }
        }
        
    }
</style>




