<template>
    <div class="cb">
        <div class="cb-div">
            <!-- 店铺名 -->
            <div class="cb-shop">
                <div class="cb-shop-left">
                    <input type="checkbox" class="selectAll" v-model="car.flag" @change="selectAll()">
                    <span>{{car.carList.shopSeller}}</span>
                </div>
                <div>
                    <span>领券</span>
                    <i class="iconfont icon-angleright"></i>
                </div>
            </div>
            <div class="cb-info">
                <div class="cb-info-left">
                    <input type="checkbox" class="selectOne" v-model="car.flag2" @change="selectOne()">
                </div>
                <div class="cb-info-right">
                    <div class="img">
                        <img :src="car.carList.img">
                    </div>
                    <div class="cb-info-right-info">
                        <p class="desc">{{car.carList.title}}</p>
                        <p>颜色尺码</p>
                        <div class="cb-price">
                            <div>
                                <b>{{car.carList.price*car.num | number}}</b>
                                <del>{{car.carList.orgPrice | number}}</del>
                            </div>
                            <div class="computer">
                                <span @click="minus()">-</span>
                                <span>{{car.num}}</span>
                                <span @click="add()">+</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import {mapState,mapGetters,mapActions,mapMutations} from "vuex";
export default {
    data(){
        return{
            // num:1,
            // flag:false,
            // flag2:false
        }
    },
    computed:{
        ...mapState(["types","car"])
    },
    filters:{
        number(value){
            return "￥"+value.toFixed(2)+"元"
        }
    },
    methods:{
        ...mapActions(["loadDataComment"]),
        ...mapActions(["setNum"]),
        minus(){
            if(this.car.num>1){
                // this.num--;
                this.setNum(this.car.num--);
            }else{
                // this.setNum(this.car.num++);
            }
        },
        add(){
            this.car.num++;
        },
        selectAll(){
            this.car.flag2=this.car.flag;
            this.car.flag3=this.car.flag;
        },
        selectOne(){
            this.car.flag=this.car.flag2;
        }
    }
}
</script>
<style lang="less">
    .cb{
        width: 100%;
        height:100%;/*px*/
        font-size: 26px;/*px*/
        input[type=checkbox]{
            width: 32px;
            height: 32px;
        }
        .cb-div{
            width: 100%;
            // height: 800px;/*px*/
            border: 1px solid #ddd;
            background: #fff;
            .cb-shop{
                width: 100%;
                height: 50px;/*px*/
                padding: 0 20px;
                border: 1px solid #ddd;
                display: flex;
                justify-content: space-between;
                align-items: center;
                .cb-shop-left{
                    width: 70%;
                    height: 100%;
                    // line-height: 100%;
                }
            }
            .cb-info{
                width: 100%;
                height: 300px;/*px*/
                display: flex;
                &-left{
                    width: 10%;
                    height: 100%;/*px*/
                    padding-left: 20px;
                    display: flex;
                    align-items: center;
                }
                &-right{
                    width: 90%;
                    height: 100%;/*px*/
                    padding: 20px 0;
                    display: flex;
                    align-items: center;
                    .img{
                        width: 180px;
                        height: 100%;/*px*/
                        margin: 0 20px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        img{
                            width: auto;
                            height: 100%;/*px*/
                        }
                    }
                    &-info{
                        width: 450px;
                        height: 100%;/*px*/
                        padding-right: 10px;
                        // background: orange;
                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;
                        .desc{
                            font-size: 24px;/*px*/
                            display: -webkit-box;
                            -webkit-box-orient: vertical;
                            -webkit-line-clamp: 3;
                            overflow: hidden;
                        }
                        .cb-price{
                            display: flex;
                            justify-content: space-between;
                            b{
                                font-size: 30px;
                            }
                            del{
                                font-size: 24px;
                            }
                            .computer{
                                width: 180px;
                                height: 40px;/*px*/
                                span{
                                    // width: 28%;
                                    height: 100%;/*px*/
                                    padding: 0 10px;
                                    display: inline-block;
                                    text-align: center;
                                    background: rgb(139, 139, 187);
                                }
                                span:nth-child(2){
                                    width: 80px;
                                }
                            }
                        }
                    }
                }
            }
        }

    }
</style>



