<template>
    <div class="tb-right wrapper" ref="wrapper_r">
        <div class="content">
            <div class="tb-clothing-parent">
                <div class="tb-clothing" v-for="(v,i) of types.right" :key="i+'i'" @click="selectClothig(v.link)">
                    <img :src="v.image">
                    <p>{{v.title}}</p>
                </div>
            </div>
            <!-- 排序  -->
            <div class="sort">
                <span v-for="(v,i) of types.sort" :key="'sort'+i" @click="selectSort(v.sortKey)">{{v.title}}</span>
            </div>
            <div class="goods-parent">
                <goods :typesgoods="types.sortbottom[i]" v-for="(v,i) of types.sortbottom" :key="'sortbottom'+i" class="goods">
                    <b slot="pink">{{v.price | numberY}}</b>   <!-- 需要过滤器 -->
                </goods>
                <div class="btw" v-if="true"></div>
            </div>
        </div>
    </div>
</template>
<script>
import Bscroll from 'better-scroll';
import {mapState,mapGetters,mapActions,mapMutations} from "vuex"
export default {
    computed:{
        ...mapState(["types","comment"])
    },
    methods:{
        ...mapActions(["loadDatasByPids","loadTypesDataTop","loadTypesDataTitle","loadTypesDataBottom","loadTypesData"]),
        ...mapMutations(["setTypeId","setCcid"]),
        selectSort(sortKey){
            this.loadTypesDataBottom({miniWallKey:this.comment.ccid,sortKey});
        },
        selectClothig(link){
            var change=this.change(link);
            this.$router.push("/typeslist?"+change);
            var str=change.slice(change.indexOf("=")+1);
            console.log(str)
            this.types.typeId=str;
            
        },
        change(link){
            var a=link.indexOf("?");
            var b=link.indexOf("&");
            var str=link.slice(a+1,b);
            return str;
        }
    },
    filters:{
        numberY(value){
            // console.log(s);
            return "￥"+value.toFixed(2)+"元";

        }
    },
    mounted() {
        this.$nextTick(() => {
            this.scroll = new Bscroll(this.$refs.wrapper_r, {
                click:true
            })
        });
        this.loadTypesDataTop("41888");
        this.loadTypesDataTitle(this.comment.ccid);

        // this.loadDatasByPids();
        
    }
}
</script>
<style lang="less">
    .tb-right{
        width: 75%;
        height: 100%;/*px*/
        .tb-clothing-parent{
            width: 100%;
            display: flex;
            flex-wrap: wrap;
            .tb-clothing{
                width: 33%;
                margin-bottom: 10px;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                margin: 15px 0;
                img{
                    width: 75%;
                }
                p{
                    margin-top: 5px;
                    font-size: 26px;
                }
            }
        }
        .sort{
            width: 90%;
            padding: 20px 0;
            margin: 20px 0 10px 5%;
            border-top: 1px solid #eee;
            border-bottom: 1px solid #eee;
            span{
                display: inline-block;
                width: 32%;
                text-align: center;
            }
            span:nth-child(2){
                border-right: 1px solid #eee;
                border-left: 1px solid #eee;
            }
        }
        .goods-parent{
            width: 100%;
            display: flex;
            flex-wrap: wrap;
            flex-grow: 2;
            justify-content: space-around;
            .goods{
                b{
                    color: #FF5777;
                    font-size: 26px;
                }
            }
            .btw{
                width: 48%;
            }
        }
        
    }
</style>




