<template>
    <div class="car">
        <car-top></car-top>
        <car-body></car-body>
        <div class="footer">
            <div class="footer-left">
                <input type="checkbox" v-model="car.flag2">
                <span class="all">全选（0）</span>
                <span class="price" v-if="car.flag3">{{types.details.price*car.num | numberY}}</span>
                <span class="price" v-else>￥00.00元</span>
            </div>
            <input type="button" value="去结算">
        </div>
    </div>
</template>
<script>
import Router from 'vue-router';
import CarTop from "./CarTop";
import CarBody from "./CarBody";
import {mapState,mapGetters,mapActions,mapMutations} from "vuex"
export default {
    data(){
        return {
            flag:false
        }
    },
    components:{
        CarTop,
        CarBody
    },
    computed:{
        ...mapState(["types","car"])
    },
    methods:{
        back(){
            this.$router.back(-1);
        },
        changeYuan(){
            this.flag=!this.flag;
        }
    },
    filters:{
        numberY(value){
            return "￥"+value.toFixed(2)+"元";
        }
    },
    mounted() {
        // if(this.car.flag3){

        // }
    },
}
</script>
<style lang="less">
    .car{
        height: 100%;
        background: #eee;
        .c-top{
            width: 100%;
            height: 80px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            border-bottom:1px solid #ccc;
            padding: 0 20px;
            i{
                font-size: 30px;
            }
            .c-text{
                font-size: 30px;/*px*/
            }
        }
        .footer{
            width: 100%;
            height: 90px;
            background: #fff;
            position: fixed;
            bottom: 0;
            padding: 0 20px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            .footer-left{
                display: flex;
                align-items: center;
                input[type=checkbox]{
                    width: 32px;
                    height: 32px;
                }
                .all{
                    margin: 0 10px;
                }
                .price{
                    color:#FC4C8B;
                }
            }
            input[type=button]{
                width: 140px;
                height: 56px;
                border: none;
                border-radius: 8px;
                background: #FC4C8B;
                font-size: 28px;
                color: #fff;
            }
        }
    }
</style>



