import api from "./../../server/api";

const state={
    left:[],
    right:[],
    sort:[],
    sortbottom:[],
    details:[],
    car:{},
    typeId:""
}
const getters={


}
const actions={
    // types左侧
    loadTypesData({commit}){
        api.loadDatasByPids({
            appPlat:"m",
            pids:"41789"
        }).then(({data})=>{
            // console.log(data)
            commit("setLeft",data[41789].list);
            // commit("setTypeId",data[41789].list)
        })
    },

    // types右侧上面
    loadTypesDataTop({commit},maitKey){
        api.loadDataByTypes({
            appPlat:"m",
            pid:maitKey
        }).then(({data})=>{
            commit("setRight",data.categoryNavigation.list)
            
        })
    },

    // types右侧中间分类title
    loadTypesDataTitle({commit},miniWallKey){
        commit("setSort",[]);
        api.loadDataBySorts({
            fcid:miniWallKey,
            page:"1",
            sort:"pop",
            cKey:"h5-cube",
        }).then(({result})=>{
            commit("setSort",result.sortFilter);
            // console.log(result.sortFilter)
        })
    },

    // types右侧下面的组件部分
    loadTypesDataBottom({commit},sort={miniWallKey,sortKey:"pop"}){
        api.loadDataBySorts({
            fcid:sort.miniWallKey,
            page:"1",
            sort:sort.sortKey,
            cKey:"h5-cube",
        }).then(({result})=>{
            commit("setSort",result.sortFilter);
            commit("setSortBottom",result.wall.docs)
            // console.log(result);
        })
    }
}

const mutations={
    setLeft(state,data){
        state.left=data;
    },
    setRight(state,data){
        state.right=data;
    },
    setSort(state,data){
        state.sort=data;
    },
    setSortBottom(state,data){
        state.sortbottom=data;
    },
    setDetails(state,data){
        state.details=data;
    },
    setCar(state,data){
        state.car=data;
    },
    setTypeId(state,data){
        state.typeId=data;
    },
}
export default {
    state,
    getters,
    actions,
    mutations
}



