<template>
	<div id="app">
		<router-view></router-view>
		<!-- <home></home> -->
		<!-- <types></types> -->
	</div>
</template>

<script>
import Home from "./components/Home";
import Types from "./components/Types";
export default {
		name: "App",    // vue仓库中的名字有关系,其他的都没有关系(仅是视觉上的关系)
		components:{
			Home,
			Types
		}
};
</script>

<style lang="less">
	*:not(.swiper-wrapper){
		margin: 0;
		padding: 0;
		overflow: hidden;
	}
	#app {
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		// background: #eee;
		font-size: 30px; /*px*/
		.active{
			color: red;
		}
	}
</style>



