import Vue from 'vue'
import Router from 'vue-router'
import Home from "./../components/Home";
import Types from "./../components/Types";
import Car from "./../components/Car";
import Mine from "./../components/Mine";
import GoodsDetails from "./../globalComponents/GoodsDetails";
import Search from "./../components/Search";
import TypesList from "./../components/TypesList";
import SearchList from "./../components/SearchList";

Vue.use(Router)

export default new Router({
    mode:"history",
    linkActiveClass:"active",
    linkExactActiveClass:"ex-active",
    routes: [
		  {path: '/', redirect:"/home"},
		  {path: '/home',name: 'home',component: Home},
		  {path: '/types',name: 'Types',component: Types},
		  {path: '/car',name: 'car',component: Car},
		  {path: '/mine',name: 'mine',component: Mine},
		  {path: '/goodsdetails',name: 'goodsdetails',component: GoodsDetails},
		  {path: '/search',name: 'search',component: Search},
		  {path: '/typeslist',name: 'typeslist',component: TypesList},
		  {path: '/searchlist',name: 'searchlist',component: SearchList}
    ]
})



