import jsonpUtil from "./../util/jsonpUtil";

const mce="https://mce.mogucdn.com/jsonp/";
const mce2="https://mce.mogujie.com/";
const mce3="https://list.mogujie.com/";
const mce4="https://rate.mogujie.com/h5/rate/jsonp/x6.rate.ratelist/v1";
// https://rate.mogujie.com/h5/rate/jsonp/x6.rate.ratelist/v1


export default {
    loadDatasByPids(params){
        let url=mce+"multiget/3";
        return jsonpUtil.request(url,params);
    },
    loadDataByTypes(params){
        let url=mce2+"jsonp/makeup/3";
        return jsonpUtil.request(url,params);
    },
    loadDataBySorts(params){
        let url=mce3+"search";
        return jsonpUtil.request(url,params);
    },
    loadDataBySearch(params){
        let url=mce3+"module/mget";
        return jsonpUtil.request(url,params);
    },
    loadDataByComment(params){
        return jsonpUtil.request(mce4,params);
    }
}




