<template>
    <!-- <router-link to="/goodsdetails" class="goods" tag="div" @click.native="details(typesgoods.iid)"> -->
    <div class="goods" tag="div" @click="selectDetails()">
        <div class="img">
            <img :src="typesgoods.img">
            <div class="posi">已售{{typesgoods.cfav}}件</div>
        </div>
        <div class="goods-desc">{{typesgoods.title}}</div>
        <div class="goods-price ss">
            <slot name="pink"></slot>
            <slot name="black"></slot>
            <div>
                <span>{{typesgoods.sale}}</span>
                <i class="iconfont icon-shoucang1"></i>
            </div>
        </div>
    </div>
</template>
<script>
import {mapState,mapGetters,mapActions,mapMutations} from "vuex";
export default {
    props:["typesgoods"],
    methods:{
        ...mapMutations(["setDetails"]),
        selectDetails(){
            this.setDetails(this.typesgoods);
            this.$router.push("/goodsdetails");
            // console.log(this.typesgoods);
        }
    },
    mounted(){
        // console.log(this.typesgoods);
    }
}
</script>

<style lang="less">
    .goods{
        width: 48%;
        background: #fff;
        border-radius: 6px;
        // 商品图片
        .img{
            width: 100%;
            position: relative;
            img{
                width: 100%;
                border-radius: 6px;
            }
            .posi{
                width: 100%;
                height: 66px;/*px*/
                color: #fff;
                font-size: 24px;/*px*/
                line-height: 66px;
                padding-left: 10px;
                background:linear-gradient(left,rgba(40,40,40, 0.8) 0%,transparent 70%);
                position: absolute;
                // top: 3.5rem;
                bottom: 5%;
                left: 0;
            }
        }
        // 商品描述
        &-desc{
            color: #666;
            padding: 0 10px;
            margin: 10px 0;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
            overflow: hidden;
        }
        // 价格、人数、星级
        &-price{
            display: flex;
            align-items: center;
            padding: 0 10px;
            margin-bottom: 10px;
            // border: 1px solid green;
            // 人数
            span,i{
                color: #666;
                font-size: 26px;/*px*/
                display: inline-block;
            }
            // 星星
            img{
                width: 30px;
                vertical-align: top;
            }
        }
        .ss{
            display: flex;
            justify-content: space-between;
        }
    }
</style>




