import Vue from 'vue';
// import Vuex from 'vuex';
import App from './App';
import router from './router';
import store from './store';
// import globalComponents from "./globalComponents/"

// // 导入全局组件
import TopBar from "./globalComponents/TopBar";
import Goods from "./globalComponents/Goods";
import GoodsDetails from "./globalComponents/GoodsDetails";
import BottomBar from "./globalComponents/BottomBar";

import "lib-flexible"

Vue.config.productionTip = false;
// Vuex.use(Vuex);
// const store=new Vuex.Store({

// });
// Vue.use(globalComponents);

// 注册全局组件
// 模板 ==> Vue.component("导入全局组件from前面的名字",组件);
Vue.component("TopBar",TopBar);
Vue.component("Goods",Goods);
Vue.component("GoodsDetails",GoodsDetails);
Vue.component("BottomBar",BottomBar);


new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



