<template>
    <div class="hb wrapper" ref="wrapper">
        <div class="content">
            <!-- swiper -->
            <div class="swiper-container">
                <swiper :options="swiperOptions" class="h-swiper">
                    <swiper-slide v-for="(v,i) of home.wrap" :key="'wrap'+i">
                        <img :src="v.image_800">
                    </swiper-slide>
                    <div class="swiper-pagination" slot="pagination"></div>
                </swiper>
            </div>
            <!-- 推荐 -->
            <div class="h-recommand">
                <div class="h-circle">
                    <div v-for="(v,i) of home.hot" :key="i">
                        <img :src="v.image">
                        <p>{{v.title}}</p>
                    </div>
                </div>
            </div>
            <!-- 热门 不规则排序 -->
            <div class="h-hot">
                <p>最潮流-促销直达</p>
                <div class="h-float">
                    <div class="h-float-div" v-for="(v,i) of home.recommand" :key="'hot'+i">
                        <div>
                            <p>{{v.title}}</p>
                            <p>{{v.viceTitle | clear}}</p>
                        </div>
                        <img :src="v.image">
                    </div>
                </div>
                <div class="clear"></div>
            </div>
            <!-- 清凉一夏 -->
            <div class="h-background">热门分类</div>
            <div class="hotHot">
                <div class="hotHot-div" v-for="(v,i) of home.hotHot" :key="'hotHot'+i">
                    <div>
                        <img :src="v.image">
                    </div>
                    <p>{{v.title}}</p>
                </div>
            </div>
            <div class="h-background">猜你喜欢</div>
            <div class="bh-comp">
                <goods :typesgoods="home.likes[i]" class="zz" v-for="(v,i) of home.likes" :key="'likes'+i">
                    <b slot="black">{{v.price | numberY}}</b>
                </goods>
                <div class="btw" v-if="true"></div>
            </div>
            <div class="clear"></div>
        </div>
    </div>
</template>
<script>
import Vue from "vue";
import Bscroll              from 'better-scroll';
import {swiper,swiperSlide} from "vue-awesome-swiper";
import {mapState,mapActions,mapGetters,mapMutations} from "vuex";

Vue.filter("clear",function(value){
    return value.replace(/[{}]/g,"");
});
export default {
    components:{
        swiper,
        swiperSlide
    },
    data(){
        return {
            swiperOptions:{
                autoplay:1000,
                loop:true,
                pagination:".swiper-pagination"
            }
        }
    },
    computed:{
        ...mapState(["home"])
    },
    filters:{
        numberY(value){
            // console.log(s);
            return "￥"+value.toFixed(2)+"元";

        }
    },
    methods:{
        ...mapActions(["loadHomeData"])
    },
    mounted() {
        this.$nextTick(() => {
            this.scroll = new Bscroll(this.$refs.wrapper, {
                click:true
            })
        }),
        this.loadHomeData();
    }
}
</script>
<style lang="less">
    .hb{
        width: 100%;
        height: 100%;/*px*/
        .swiper-container{
            width: 100%;
            // height: 260px;/*px*/
            img{
                width: 100%;
                height: auto;
            }
            .swiper-pagination-bullet{
                width: 14px;/*px*/
                height: 14px;/*px*/
            }
            .swiper-pagination-bullet-active{
                background: #d43e2e;
            }
            .swiper-pagination-bullets .swiper-pagination-bullet{
                margin-right: 16px;/*px*/
            }
            .swiper-container-horizontal>.swiper-pagination-bullets{
                width: 30%;
                
            }
        }
        .h-recommand{
            width: 100%;
            background: #fff;
            .h-circle{
                width: 100%;
                display: flex;
                // padding: 10px 0;
                flex-wrap: wrap;
                margin: 10px 0;
                >div{
                    text-align: center;
                    width: 20%;
                    margin: 10px 0;
                }
                img{
                    width: 80%;
                    height: auto;/*px*/
                    border-radius: 50%;
                }
                p{
                    font-size: 24px;
                }
            }
        }
        .h-hot{
            margin-top: 10px;/*px*/
            // height: 700px;/*px*/
            background: #fff;
            margin-bottom: 10px;/*px*/
            >p{
                padding: 20px;/*px*/
                font-size: 30px;/*px*/
                border-bottom: 0.6px solid #ccc;
            }
            .h-float-div{
                width: 33.3%;
                height: 300px;
                float: left;
                border:1px solid #ccc;
                display: flex;
                flex-direction: column;
                align-items: center;
                div{
                    text-align: center;
                    font-size: 24px;/*px*/
                    // border:1px solid green;
                    margin: 20px 0;/*px*/
                    p:nth-child(2){
                        color:#FD617F;
                    }
                }
                img{
                    width: 130px;
                }
            }
            .h-float-div:nth-child(1){
                width:50%;
                p{
                    font-size: 28px;/*px*/
                }
                img{
                    width: 130px;
                }
            }
            .h-float-div:nth-child(2),.h-float-div:nth-child(3){
                width:50%;
                height: 150px;
                display: flex;
                flex-direction: row;
                justify-content: center;
                p{
                    text-align: left;
                }
                img{
                    margin-left: 20px;
                    width: 100px;
                }
            }
        }
        .hotHot{
            width: 100%;
            background: #fff;
            display: flex;
            justify-content: space-around;
            flex-flow: wrap;
            flex-grow: 4;
            &-div{
                width: 25%;
                border:1px solid green;
                padding: 10px 0;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                align-items: center;
                img{
                    width: 100%;
                    height: auto;
                    border-radius: 100%;
                }
                p{
                    font-size: 26px;/*px*/
                }
            }
        }
        .bh-comp{
            width: 100%;
            padding: 0 5px;
            display: flex;
            flex-wrap: wrap;
            flex-grow: 2;
            justify-content: space-around;
            .zz{
                margin-top: 16px;
            }
            .btw{
                width: 48%;
            }
        }
        .h-background{
            height: 70px;/*px*/
            line-height: 70px;
            margin: 0 auto;
            background: url(https://s10.mogucdn.com/p1/160415/upload_ifrwkyzymu2damdgg4zdambqhayde_313x10.png) no-repeat;
            background-position: 50% 50%;
            background-size: 400px;
            text-align: center;
            font-size: 30px;/*px*/
        }
    }
</style>



