<template>
    <div class="types">
        <!-- top导航 -->
        <top-bar class="top-fixed"></top-bar>
        <div class="tb-parent">
            <types-body class="types-body"></types-body>
        </div>
        <!-- 底部按钮 -->
        <bottom-bar class="bottom-fixed"></bottom-bar>
    </div>
</template>
<script>
import TypesBody from './../components/TypesBody';
export default {
    components:{
        TypesBody
    }
    
}
</script>
<style lang="less">
     .types{
         height: 100%;/*px*/
        .top-fixed{
            position: fixed;
            top: 0;
            z-index: 99;
        }
        .tb-parent{
            height: 100%;/*px*/
            padding: 100px 0;
        }
        .bottom-fixed{
            position: fixed;
            bottom: 0;
        }
    }
</style>



