<template>
    <div class="c-top">
        <div class="c-back" @click="back()">
            <i class="iconfont icon-fanhui"></i>
        </div>
        <div class="c-text">购物车</div>
        <div class="c-editer">
            <span>编辑</span>
            <i class="iconfont icon-xx"></i>
        </div>
    </div>
</template>
<script>
export default {
    methods:{
        back(){
            this.$router.back(-1);
        }
    }
}
</script>

<style lang="less">
    .c-top{
        width: 100%;
        height: 80px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom:1px solid #ccc;
        padding: 0 20px;
        i{
            font-size: 30px;
        }
        .c-text{
            font-size: 30px;/*px*/
        }
    }
</style>




