import api from "./../../server/api";
const state={
    wrap:[],
    hotHot:[],
    recommand:[],
    hot:[],
    likes:[]
}
const getters={

}
const actions={
    loadHomeData({commit}){
        api.loadDatasByPids({
            appPlat:"m",
            pids:"51822,106930,51833,51836"
        }).then(({data})=>{
            // 轮播图
            commit("setWrap",data[51822].list);
            // 热门分类
            commit("setHot",data[106930].list);
            // 推荐 不规则排序
            commit("setRecommand",data[51833].list);
            // 最潮流-促销直达
            commit("sethotHot",data[51836].list);
        }),

        // 猜你喜欢
        api.loadDataBySorts({
            pid:"9750",
            sort:"pop",
            cKey:46
        }).then(({result})=>{
            commit("setLikes",result.wall.docs);
        })
    }

}
const mutations={
    setWrap(state,data){
        state.wrap=data;
    },
    sethotHot(state,data){
        state.hotHot=data;
    },
    setRecommand(state,data){
        state.recommand=data;
    },
    setHot(state,data){
        state.hot=data;
    },
    setLikes(state,data){
        state.likes=data;
    }
}
export default {
    state,
    actions,
    getters,
    mutations
}



