import api from "./../../server/api";

const state={
    comment:[],
    ccid:"10062603"
    
}

const getters={

}

const actions={
    loadDataComment({commit},ccid){
        api.loadDataByComment({
            type:"1",
            itemId:ccid,
            appPlat:"m"
        }).then(({data})=>{
            commit("setComment",data);
            // console.log("ccccccc",data);
        })
    }
}

const mutations={
    setComment(state,data){
        state.comment=data;
    },
    setCcid(state,data){
        state.ccid=data;
    }
}
export default {
    state,
    getters,
    actions,
    mutations
}


